.products {
  width: 100%;
  height: auto;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  justify-content: center;
  align-items: center;
}



.product {
  /* border-radius: 15px; */
  max-width: 300px; 
  height: 350px;
  margin: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.product img {
    width: 300px;
}

.product .desc {
    text-align: center;
}

.product:hover {
    cursor: pointer;
}

.addToCartBtn {
  transition: .3s;
  background-color: transparent;
  border: 2px solid rgb(19, 19, 19);
  min-width: 100px;
  padding: 5px 10px;
  border-radius: 15px;
}

.addToCartBtn:hover {
  background-color: rgb(19, 19, 19);
  color: white;
  cursor: pointer;
}