.header {
  width: 100%;
  /* height: fit-content; */
  background-color: rgb(200, 200, 200);
  padding: 20px 0;

}
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.head .link{
    padding: 0 40px;
    font-size: 25px;
    font-weight: bold;
    color: rgb(39, 179, 179);
    cursor:pointer;
    text-decoration: none;
}

.links {
  margin-right: 50px;
  display: flex;
  align-items: center;
}

.links a {
  text-decoration: none;
  color: rgb(39, 179, 179);
  margin-left: 20px;
  font-size: 25px;
}
.links a:hover,
.head .link :hover {
    color: rgb(39, 159, 159);
}
.shopTitle {
  margin-top: 40px;
  text-align: center;
  font-size: 25px;
}